import { View } from 'react-native';

import type { PrimaryColumnProps, SecondaryColumnProps, SupplementaryColumnProps } from './types';

export function PrimaryColumn(props: PrimaryColumnProps) {
  return <View {...props} />;
}
export function SupplementaryColumn(props: SupplementaryColumnProps) {
  return <View {...props} />;
}
export function SecondaryColumn(props: SecondaryColumnProps) {
  return <View {...props} />;
}
